import mongoose, { Schema, Model, Document } from "mongoose";
import connectDB from "@/lib/mongodb";

// Ensure database connection
if (mongoose.connection.readyState === 0) {
  connectDB().catch((err) => console.error("MongoDB connection error:", err));
}

// TypeScript interfaces for type safety
export interface ILeadInput {
  name: string;
  title: string;
  company: string;
  email?: string;
}

export interface IResearch {
  CompanyProfile?: mongoose.Schema.Types.Mixed;
  PersonaProfile?: mongoose.Schema.Types.Mixed;
  DetectedSignals?: mongoose.Schema.Types.Mixed[];
  ResearchMeta?: mongoose.Schema.Types.Mixed;
}

export interface IICP {
  icp_score: number;
  fit_tier: string;
  score_breakdown?: mongoose.Schema.Types.Mixed;
  strengths: string[];
  risks: string[];
  gaps: string[];
  confidence_level: string;
  scoring_meta?: mongoose.Schema.Types.Mixed;
}

export type LeadStatus = "uploaded" | "researched" | "icp_scored";

export interface ILead extends Document {
  lead_input: ILeadInput;
  research?: IResearch;
  icp?: IICP;
  status: LeadStatus;
  createdAt: Date;
  updatedAt: Date;
}

// Schema definition
const LeadSchema = new Schema<ILead>(
  {
    lead_input: {
      name: {
        type: String,
        required: true,
      },
      title: {
        type: String,
        required: true,
      },
      company: {
        type: String,
        required: true,
      },
      email: {
        type: String,
        required: false,
      },
    },
    research: {
      CompanyProfile: {
        type: Schema.Types.Mixed,
        required: false,
      },
      PersonaProfile: {
        type: Schema.Types.Mixed,
        required: false,
      },
      DetectedSignals: {
        type: [Schema.Types.Mixed],
        required: false,
        default: [],
      },
      ResearchMeta: {
        type: Schema.Types.Mixed,
        required: false,
      },
    },
    icp: {
      icp_score: {
        type: Number,
        required: false,
      },
      fit_tier: {
        type: String,
        required: false,
      },
      score_breakdown: {
        type: Schema.Types.Mixed,
        required: false,
      },
      strengths: {
        type: [String],
        required: false,
        default: [],
      },
      risks: {
        type: [String],
        required: false,
        default: [],
      },
      gaps: {
        type: [String],
        required: false,
        default: [],
      },
      confidence_level: {
        type: String,
        required: false,
      },
      scoring_meta: {
        type: Schema.Types.Mixed,
        required: false,
      },
    },
    status: {
      type: String,
      enum: ["uploaded", "researched", "icp_scored"],
      required: true,
      default: "uploaded",
    },
  },
  {
    timestamps: true,
    collection: "leads",
  }
);

// Export model safely to avoid re-registration errors
const Lead: Model<ILead> =
  mongoose.models.Lead || mongoose.model<ILead>("Lead", LeadSchema);

export default Lead;
